	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
	export default {
		components: {
			uniLoadMore	
		},
		data() {
			return {
				noticeList: [],
				listQuery: { //查询分页
					name:"", 
					pageNo: 1,
					pageSize: 10
				},
			}
		},
		onLoad(options){ 
			this.loadData();
		},
		//下拉刷新
		onPullDownRefresh(){
			this.listQuery.pageNo=1;
			this.loadData('refresh');
		},
		//加载更多
		onReachBottom(){
			this.listQuery.pageNo++;
			this.loadData();
		},
		methods: {
			imgPath:function(path){
				if(path==''||path==null||path=='null'){
					return "/static/errorImage.jpg"				 
				}else if (path!=''&&path.indexOf('http')>=0) {
					return path;				 
				}
				return this.siteHost + path;
			},
			//加载商品 ，带下拉刷新和上滑加载
			async loadData(type='add', loading) {
				//没有更多直接返回
				if(type === 'add'){
					if(this.loadingType === 'nomore'){
						return;
					}
					this.loadingType = 'loading';
				}else{
					this.loadingType = 'more'
				}
				//加载第一页
				if(type === 'refresh'){
					if(loading == 1){
						this.listQuery.pageNo=1;
					}
				}
 				let _this=this;
				this.sendRequest({
					url : "get_notice_list",
					data : this.listQuery,
					method : "get",
					header: {'content-type' : "application/x-www-form-urlencoded"},
					success:function (res) {
						let pageData= res.data;  
						let noticeList = pageData.datas;
						if(type === 'refresh'){
							_this.noticeList = [];
						}
						_this.noticeList = _this.noticeList.concat(noticeList); 
						//判断是否还有下一页，有是more  没有是nomore(测试数据判断大于20就没有了)
						_this.loadingType  = pageData.pageNo<=pageData.totalPage ? 'nomore' : 'more';
						if(type === 'refresh'){
							if(loading == 1){
								uni.hideLoading()
							}else{
								uni.stopPullDownRefresh();
							}
						}
					}
				}); 
			},
			detail(item){
				if (item.url!=''&&item.url.indexOf('http')>=0) {
					window.location.href =item.url;
				}else{
					uni.navigateTo({
						url: item.url
					})
				} 
			}
		}
	}
